import http from '@/components/utils/http.js'
export default {
	data() {
		return {
			title: '',
			content: ''
		}
	},
	// 接收newsid
	onLoad(e) {
		// uni.showLoading({
		// 	title: '加载中...',
		// 	// mask: false
		// });
		console.log(e)
		let opts = {
			url: '/api/news/newsDetail',
			method: 'GET',
		};
		let param = {
			id: e.newsid
		};
		http.httpRequest(opts, param).then(res => {
				if(res.data.code == 1){
					console.log(res);
					this.title = res.data.data.title;
					this.content = res.data.data.content;	
					// this.$store.state.title=this.title;
					uni.setNavigationBarTitle({
						title: this.title
					})
				}else{
					uni.showToast({
						title: "加载失败..."
					});
				}
			},error => {
			console.log(error)
		})
	},
	methods: {
		
	}
}
